@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 20px;
  @media (max-width: 1300px) {
    padding: 20px 0;
  }
  @media (max-width: 803px) {
    padding: 10px 0;
  }
  .container {
    display: flex;
    align-items: center;
  }

  &__logo {
    //TODO: при нажатии на поиск сделать исчезание лого и корзины - поиск при этом займет полностью всю область
    //TODO: так же что бы корзина на маленьких экранах была без пункта стоимости, тоесть левую часть обрезать
    display: flex;
    margin-right: 50px;

    @media (max-width: 950px) {
      margin-right: 20px;
    }
    @media (max-width: 460px) {
      margin-right: 0px;
    }
    img {
      margin-right: 15px;
      @media (max-width: 630px) {
        margin-right: 8px;
      }
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
      @media (max-width: 630px) {
        font-size: 20px;
      }
    }

    p {
      color: #7b7b7b;
      @media (max-width: 630px) {
        font-size: 12px;
      }
      @media (max-width: 420px) {
        font-size: 10px;
      }
      @media (max-width: 360px) {
        font-size: 9px;
      }
      @media (max-width: 330px) {
        font-size: 8px;
      }
    }
  }
}
