@import '../variables';

.pizza-block {
  display: flex;
  flex-direction: column;
  justify-self: center;

  width: 280px;
  text-align: center;
  margin: 0px 10px 65px;
  @media (max-width: 1500px) {
    margin: 0px 10px 30px;
  }
  @media (max-width: 1090px) {
    margin: 0px 5px 35px;
  }
  @media (max-width: 670px) {
    margin: 0px 0px 35px;
  }

  &__image {
    display: flex;
    padding-left: 10px;
    align-self: center;
    justify-self: center;
    min-width: 260px;
    min-height: 250px;
  }

  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    height: 55px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}
